import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { getMascotas } from "../../../services/mascotas";

const MascotasTabla = () => {
  const [mascotas, setMascotas] = useState([]);

  // utilizamos el historial
  const history = useHistory();

  // redireccionamos al dar clic
  const handleClick = (mascota_id) => {
    history.push(`/mascotas/${mascota_id}`);
  };

  const traerMascotas = async () => {
    getMascotas().then((data) => {
      setMascotas(data);
    });

    // getMascotas().then(setMascotas); // setMascotas haciendo un callback
  };

  useEffect(() => {
    traerMascotas();
  }, []);

  return (
    <div className="row mt-4">
      <div className="table-responsive">
        <table className="table table-striped table-hover table-bordered text-center">
          <thead>
            <tr>
              <th>Id</th>
              <th>Nombre</th>
              <th>Colores</th>
              <th>Activo</th>
              <th>Acciones</th>
            </tr>
          </thead>
          <tbody>
            {mascotas.map((m) => (
              <tr key={m.mascota_id}>
                <td>{m.mascota_id}</td>
                <td>{m.mascota_nombre}</td>
                <td>{m.mascota_colores}</td>
                <td>{m.mascota_activo.toString()}</td>
                <td>
                  <button
                    type="button"
                    className="btn btn-secondary btn-block"
                    onClick={() => {
                      handleClick(m.mascota_id);
                    }}
                  >
                    Ver Detalles
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default MascotasTabla;
