import React, { useEffect, useState } from "react";
import { getMascotasById } from "../../services/mascotas";

const dataVacia = {
  mascota_nombre: "",
  mascota_raza: 0,
  mascota_tipo: 0,
  mascota_edad: 0,
  mascota_colores: "",
  mascota_activo: false,
};

const MascotaVer = (props) => {
  const { mascota_id } = props.match.params;

  const [mascota, setMascota] = useState(dataVacia);

  // console.log(props);
  // console.log(props.match.params.mascota_id);

  const traerMascotaById = async (mascota_id) => {
    getMascotasById(mascota_id).then((data) => {
      setMascota(data);
      console.log(mascota);
    });
  };

  useEffect(() => {
    traerMascotaById(mascota_id);
  }, []);

  return (
    <div className="container">
      VIENDO LA INFORMACION DE LA MASCOTA: <strong>{mascota_id}</strong>
      <div className="card">
        <div className="row ">
          <div className="col-md-6 col-sm-12">
            <figure className="text-center" style={{ height: "399px" }}>
              <img
                src="../animales-transparente.png"
                className="img-fluid h-100"
                alt=""
              />
            </figure>
          </div>
          <div className="col-md-6 col-sm-12 col-12">
            <div className="card h-100">
              <h4 className="card-title display-2 pl-5" name="mascota_nombre">
                {mascota.mascota_nombre}
              </h4>
              <div className="pl-5">
                {/* TIPO */}
                <div className="form-group row">
                  <div className="form-group col-md-5 col-sm-4 col-4">Tipo</div>
                  <div className="form-group col-md-6 col-sm-7 col-7">
                    {mascota.mascota_tipo}
                  </div>
                </div>

                {/* RAZA */}
                <div className="form-group row">
                  <div className="form-group col-md-5 col-sm-4 col-4">Raza</div>
                  <div className="form-group col-md-6 col-sm-7 col-7">
                    {mascota.mascota_raza}
                  </div>
                </div>

                {/* EDAD */}
                <div className="form-group row">
                  <div className="form-group col-md-5 col-sm-4 col-4">Edad</div>
                  <div className="form-group col-md-6 col-sm-7 col-7">
                    {mascota.mascota_edad}
                  </div>
                </div>

                {/* COLORES */}
                <div className="form-group row">
                  <div className="form-group col-md-5 col-sm-4 col-4">
                    Colores
                  </div>
                  <div className="form-group col-md-6 col-sm-7 col-7">
                    {mascota.mascota_colores}
                  </div>
                </div>

                {/* REGISTRO ACTIVO */}
                <div className="form-group row">
                  <div className="form-group col-md-5 col-sm-4 col-4">
                    Registro activo
                  </div>
                  <div className="form-group col-md-6 col-sm-7 col-7">
                    {mascota.mascota_activo.toString()}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MascotaVer;
